}
.nav-items{
    padding: 0px 20px;
    display: flexbox;
}
.nav-items >ul {
    font-size: 14px;
    display: flex;
    list-style-type: none;
    padding: 0px 20px;
}
.nav-items >ul  >li {
    padding: 10px;
    margin: 10px;

}

.res-card{
